.login-content {
  display: flex;
  padding-top: 100px;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .login-content .login-border {
    margin-top: 1rem;
    margin-bottom: 1rem; }
  .login-content .login-stuff {
    margin-top: auto;
    margin-bottom: auto; }
    .login-content .login-stuff .login-logo {
      flex: 1 1; }
    .login-content .login-stuff .login-tagline h2 {
      color: #FFFFFF;
      font-family: AvenirNext-UltraLight;
      padding: 0 10px;
      text-transform: uppercase; }
    .login-content .login-stuff .submit-button {
      display: flex; }
      .login-content .login-stuff .submit-button .button {
        flex: 1 1;
        background-color: #00BCD4;
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        font-size: 16px; }

.left-pane-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 3em; }
  .left-pane-container .logo {
    flex: 0 0 auto;
    align-self: center; }
  .left-pane-container .links-container {
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-top: 3em; }
    .left-pane-container .links-container .link-item {
      margin-bottom: 2em; }

.middle-pane-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 6em;
  margin-left: 3em;
  margin-right: 3em;
  margin-bottom: 3em; }
  .middle-pane-container h1 {
    background: -webkit-linear-gradient(#5bc2d0, #9b91d4);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

.dashboard-data-list {
  display: flex;
  flex-direction: column;
  margin-top: 1em; }
  .dashboard-data-list .dashboard-data-item {
    display: flex;
    flex-direction: row; }
    .dashboard-data-list .dashboard-data-item .data-icon {
      margin-right: 1em; }
    .dashboard-data-list .dashboard-data-item .data {
      display: flex;
      flex-direction: column; }
      .dashboard-data-list .dashboard-data-item .data .data-title {
        flex: 1 1 auto;
        font-weight: bold; }
      .dashboard-data-list .dashboard-data-item .data .data-details {
        flex: 1 1 auto; }
      .dashboard-data-list .dashboard-data-item .data .data-action {
        flex: 1 1 auto; }
        .dashboard-data-list .dashboard-data-item .data .data-action .action-button {
          background-color: #f08e38;
          color: #fbe5d0;
          border: none;
          text-align: center;
          text-decoration: none;
          font-size: 16px;
          padding: 5px 15px;
          border-radius: 5px; }

.right-pane-container {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .right-pane-container .user-widget {
    display: flex;
    flex-direction: row;
    justify-content: flex-end; }

.dashboard-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 100vh; }
  .dashboard-container .left-side {
    flex: 1 1 auto;
    color: white; }
  .dashboard-container .middle {
    flex: 7 1 auto;
    background-color: white; }
  .dashboard-container .right-side {
    flex: 5 1 auto;
    color: white; }

body {
  margin: 0;
  padding: 0;  
  background-image: -webkit-gradient(linear,left top, right bottom,from(#3022AD), to(#894EC6));  
  background-image: -webkit-linear-gradient(top left,#3022AD, #894EC6);  
  background-image: linear-gradient(to bottom right,#3022AD, #894EC6);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html {
  height: 100%;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

